* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Inter', sans-serif;
}
.header{
  display: flex;
  height: 65px;
  align-items: center;
  padding: 0 30px;
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  color: white;
}
.logo{
  width: 30px;
  margin-right: 10px;
}
.logo-text{
  font-weight: 600;
}
.project-title{
  margin-left: auto;
}
.main{
  padding: 60px 30px;
}
.form{
  display: grid;
  grid-template: 40px 40px / 50% 50%;
  gap: 20px;
}
input{
  text-indent: 10px;
  border: 1px solid #D5D4D8;
  border-radius: 4px;
}
.generate-btn{
  grid-column: 1/-1;
  border: none;
  border-radius: 4px;
  color: white;
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  cursor: pointer;
}
.meme{
  position: relative;
}
.meme-image{
  margin: 20px auto;
  display: block;
  width:400px;
  max-width: 100%;
}
.meme .text{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 36px;
  font-weight: bold;
  color: white;
  text-shadow: -1px 1px 0 #000,
    1px 1px 0 #000,
  1px -1px 0 #000,
  -1px -1px 0 #000;
}
.meme .top-text{
  top: 10px;
}
.meme .bottom-text{
  bottom: 10px;
}